//引入
import Vue from 'vue';
import VueRouter from 'vue-router';

import TopicList from '@/components/TopicList.vue';
import Topic from '@/components/Topic.vue';
import Login from '@/components/Login.vue';
import Logout from '@/components/Logout.vue';
import Msg from '@/components/Msg.vue';
import Center from '@/components/Center.vue';

import User from '@/components/User.vue'
import Create from '@/components/Create.vue'

import Edit from '@/components/Edit.vue'
//全局注册
Vue.use(VueRouter);

//定义路由信息对象
const routes = [
  //{path:'/',name:'home',component:Home},
  {path:'/',redirect:'/topiclist/all'},//路由重定向
  {path:'/topiclist/:name',name:'topiclist',component:TopicList,
props:true},//表示把动态路由参数作为props属性传递  或者对外输出时用：$route
{path:'/topic/:id',name:'topic',component:Topic,props:true},//路由路径 路由组件 路由名称等内容
//登录操作
{path:'/Login',name:'login',component:Login},
//个人中心
{path:'/Center',name:'center',component:Center},
{path:'/user/:loginname',name:'user',component:User,props:true},
//消息通知
{path:'/Msg',name:'msg',component:Msg},
//退出
{path:'/Logout',name:'logout',component:Logout},
//发表新话题
{path:'/create', name:'create',component:Create},
//编辑主题
{path:'/topic/:id/edit',name:'edit',component:Edit,props:true}
];


//创建路由管理器
const router = new VueRouter({
  routes
});//实现路由注册 路由跳转 编程时路由导航
//对外输出模块
export default router;


