//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';
//全局注册
Vue.use(Vuex);

//创建Store对象
const store = new Vuex.Store({
    //状态数据
    state:{
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'}
          ],
          unLoginedMmoreMenus:{
            login:'登录',
            
          },
          logindeMoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            logout:'退出'
          },
          isLogind:false,
          //登录用户名
          loginname:'',
          //登录用户的accesstoken
          accesstoken:'',
          //登录用户id
          author_id:'',
          //是否显示更多菜单
          isShowMoreMenus:false
    },
    //状态计算属性
    getters:{
        moreMenus: function(state) {
            if (state.isLogind) {
              //用户处于登录状态，返回登录后的跟多菜单
              return state.logindeMoreMenus;
            }
            return state.unLoginedMmoreMenus;
          },
          getTopicDescByTab: function(state){
            //需要返回一个函数结构
            return function(tab){
              for(var index=0;index<state.topics.length;index++){
                if(state.topics[index].name == tab){
                  return state.topics[index].desc
                }
              }
            }
          }
    },
    mutations,
    actions
})


//对外输出模块
export default store;


